import React from "react";

// reactstrap components
import {
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  Row,
  Col
} from "reactstrap";

//importamos el contexto
import { Consumer } from '../../Config/Context';

function Contact(){

	return(
		<Consumer>
		{ ({idioma,Config}) => {
			let contactos = Config.body[idioma].contactos
			let  { titulo, inputName, inputEmail, inputMessage, messagePlaceHolder,inputButton,inputTelefono}  = contactos
			return(
					<>
				        <div className="section landing-section" id={"seccion-contactos"}>
				          <Container>
				            <Row>
				              <Col className="ml-auto mr-auto" md="8">
								<h2 className="text-center">{titulo}</h2>
				                <Form className="contact-form">
				                  <Row>
				                    <Col md="6">
				                      <label>{inputName}</label>
				                      <InputGroup>
				                        <InputGroupAddon addonType="prepend">
				                          <InputGroupText>
				                            <i className="nc-icon nc-single-02" />
				                          </InputGroupText>
				                        </InputGroupAddon>
				                        <Input placeholder={inputName} type="text" />
				                      </InputGroup>
				                    </Col>
				                    <Col md="6">
				                      <label>{inputTelefono}</label>
				                      <InputGroup>
				                        <InputGroupAddon addonType="prepend">
				                          <InputGroupText>
				                            <i className="nc-icon nc-mobile" />
				                          </InputGroupText>
				                        </InputGroupAddon>
				                        <Input placeholder={inputTelefono} type="text" />
				                      </InputGroup>
				                    </Col>
				                    <Col md="12">
				                      <label>{inputEmail}</label>
				                      <InputGroup>
				                        <InputGroupAddon addonType="prepend">
				                          <InputGroupText>
				                            <i className="nc-icon nc-email-85" />
				                          </InputGroupText>
				                        </InputGroupAddon>
				                        <Input placeholder={inputEmail} type="text" />
				                      </InputGroup>
				                    </Col>

				                  </Row>
				                  <label>{inputMessage}</label>
				                  <Input
				                    placeholder={messagePlaceHolder}
				                    type="textarea"
				                    rows="4"
				                  />
				                  <Row>
				                    <Col className="ml-auto mr-auto" md="4">
				                      <Button className="btn-fill" color="danger" size="lg">
				                        {inputButton}
				                      </Button>
				                    </Col>
				                  </Row>
				                </Form>
				              </Col>
				            </Row>
				          </Container>
				        </div>
					</>
			)
		}}
		</Consumer>
	)
}

export default Contact;