import React from "react";

// reactstrap components
import {
  Container,
  Row,
  Col
} from "reactstrap";

import { Consumer } from '../../Config/Context';

// import Animate from "subcomponents/Animate";

// const animacionTelefono = {
//   animacion : "bounceIn",
//   tiempo : "6s linear infinite"
// }

function Mapa(){

	return(
		<Consumer>
			{ ({ idioma, Config }) => {
				let direccion = Config.body[idioma].direccion
				let {ubicacion, telefono, titulo} = direccion
				return(
						<>
					        <div className="section landing-section">
				{/*
					        <div className="section landing-section profile-content"
							style={{  
								backgroundImage: "url(" + imagen + ")",
								backgroundColor: "#cccccc",
								backgroundRepeat: "no-repeat",
								backgroundSize: "cover",
								marginTop:"70px"
							}}
					        >
				*/}
					          <Container>
					            <Row>
					              <Col className="ml-auto mr-auto" md="6" 
					              style={{
										padding:"20px",
										margin:"20px",
									}}>
									<h2 className="text-center">{titulo}</h2>
					              </Col>
					            </Row>

					            <Row>
					              <Col className="ml-auto text-center" md="3">
										<div className="card-body">
												<SubComponent elemento={ubicacion} />
												<SubComponent elemento={telefono} />
											</div>
					              </Col>
					              <Col className="ml-auto" md="9">
									<div style={{

									}}>
											<iframe 
													src={"https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d902.0580086122307!2d-57.595365170818944!3d-25.262778786149937!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zMjXCsDE1JzQ2LjAiUyA1N8KwMzUnNDEuMyJX!5e0!3m2!1ses!2spy!4v1583454004829!5m2!1ses!2spy" }
													width={"100%"} 
													height={"450"} 
													frameBorder={"0"} 
													style={{"border":"0"}} 
													allowFullScreen={""}
													title="Mapa"
													>
											</iframe>
									</div>
					              </Col>	              
					            </Row>

					          </Container>
					        </div>			

						</>
				)
			}}
		</Consumer>
	)
}

function SubComponent(props){
	// console.log(props)
	let  {elemento}  = props
	let { descripcion,icon } = elemento
	return(
				<div className="info info-horizontal" id={"seccion-mapa"}>
					<div className="icon icon-info">
						<i className={"nc-icon " + elemento.icon}></i>
					</div>
					<div className="description">
						<h4 className="info-title">{elemento.titulo}</h4>
						{descripcion.map( (currentValue, index, array) => (
							<p key={"sub_"+index}>{currentValue}</p>
						))}
					</div>
				</div>
	)
}

export default Mapa;