import React from "react";
// reactstrap components
//importamos el contexto
import { Consumer } from 'Config/Context';
import Servicio from 'subcomponents/Servicio';

import {
  Button,
  Container,
  Row,
  Col
} from "reactstrap";

function Servicios(){

	return(
    <Consumer>
    {( { idioma, Config } ) => {
      let servicios = Config.body[idioma].servicios
      // console.log(servicios)
      let {titulo,intro,contenido} = servicios 
      return(
  		<>
       <div className="section text-center" id={"seccion-servicios"}>
          <Container>
            <div className="text-center">
              <div className="icon">
                <i className={"nc-icon turqueza titulo-size screwdriver-and-wrench"}/>
              </div>
            </div>
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <h2 className="title">{titulo}</h2>
                <h5 className="description">
                {intro}
                </h5>
                <br />
                <Button
                  className="btn-round"
                  color="info"
                  href="#pablo"
                  onClick={e => e.preventDefault()}
                >
                  See Details
                </Button>
              </Col>
            </Row>
            <br />
            <br />
            <Row>
            { 
              contenido.map( (currentValue, index, array) => <Servicio key={index} contenido={currentValue}/>)
            }
            </Row>
          </Container>
        </div>
      </>		
      )
    }}
    </Consumer>
	)
}

export default Servicios;