import React from "react";

// reactstrap components
import { Row, Container } from "reactstrap";

import { Link as LinkScroll, animateScroll } from 'react-scroll';

import { Consumer } from 'Config/Context';

function DemoFooter() {
  return(
  <Consumer>
    { ( { idioma,Config,handleClick } )  => {
      let menuFooter = Config.header[idioma].menu;
      return (
        <footer className="footer footer-black footer-white section-dark">
          <Container>
            <Row>
              <nav className="footer-nav">
                <ul>
                  {
                    menuFooter
                    .filter( (objeto) => {
                      return objeto.tipo==="menu"
                    })
                    .map( (current, index, array) =>{
                      return(
                            <li key={"footer_"+index}>
                              <LinkScroll
                                className="nav-link pointer"
                                smooth={true}
                                spy={true}
                                offset={-20}
                                color="transparent"                        
                                to={current.id}>
                                        {current.descripcion}
                              </LinkScroll>
                            </li>
                      )
                    }) 
                  }
                </ul>
              </nav>
              <div className="credits ml-auto">
                <span className="copyright">
                  © {new Date().getFullYear()}, made with{" "}
                  <i className="fa fa-heart heart" /> by $$$
                </span>
              </div>
            </Row>
          </Container>
        </footer>
      );
    } }
  </Consumer>
  )
}

export default DemoFooter;
