import React from "react";
import PropTypes from 'prop-types'; 
//importamos el contexto
import { Consumer } from 'Config/Context';

import {
  Container,
  Row,
  Col
} from "reactstrap";

import Animate from 'subcomponents/Animate';

// const configuracionAnimacion = { 
//   animacion : "fadeIn",
//   tiempo : "5s",
//   clase:""
// }

function Nosotros(){

	return(
    <Consumer>
    {( { idioma, Config } ) => {
      let nosotros = Config.body[idioma].nosotros
      let {titulo,descripcion} = nosotros 
      return(
  		<>
       <div className="section text-center" id={"seccion-nosotros"}>
          <Container>
            <div className="text-center">
                <div className="icon">
                  <i className={"nc-icon turqueza titulo-size nosotros"}/>
                </div>
            </div>
            <Row>
              <Col className="ml-auto mr-auto" md="8">
                <Animate>
                  <h2 className="title">{titulo}</h2>
                </Animate>
                <h5 className="description">
                {descripcion}
                </h5>
              </Col>
            </Row>
          </Container>
        </div>
      </>		
      )
    }}
    </Consumer>
	)
}

Nosotros.propTypes = {
  titulo: PropTypes.string,
  descripcion: PropTypes.string
}

export default Nosotros;