import React from "react";

// reactstrap components
import {
  Card,
  Container,
  Row,
  Col,
  Carousel,
  CarouselItem,
  CarouselIndicators,
  CarouselCaption
} from "reactstrap";

import { Consumer } from 'Config/Context';
// core components

const items = [
  {
    src: require("assets/img/galeria/1.jpg"),
    altText: "Somewhere",
    caption: "Somewhere"
  },
  {
    src: require("assets/img/galeria/2.jpg"),
    altText: "Somewhere else",
    caption: "Somewhere else"
  },
  {
    src: require("assets/img/galeria/3.jpg"),
    altText: "Here it is",
    caption: "Here it is"
  }
];

function SectionCarousel() {
  const [activeIndex, setActiveIndex] = React.useState(0);
  const [animating, setAnimating] = React.useState(false);
  const onExiting = () => {
    setAnimating(true);
  };
  const onExited = () => {
    setAnimating(false);
  };
  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  };
  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  };
  const goToIndex = newIndex => {
    if (animating) return;
    setActiveIndex(newIndex);
  };
  return (
    <Consumer>
      { ( { idioma, Config } ) => {
        let galeria = Config.body[idioma].galeria;
        let {titulo,descripcion}  = galeria;
        return(
          <>
            <div className="section pt-o" id="carousel">
              <Container>
                
                   <div className="text-center">
                    <div className="icon">
                      <i className={"nc-icon turqueza titulo-size fix-sign"}/>
                      </div>
                     <h2 className="title">{titulo}</h2>
                    </div>
                <Row>
                  <Col className="ml-auto mr-auto" md="8">
                    <Card className="page-carousel">
                      <Carousel
                        activeIndex={activeIndex}
                        next={next}
                        previous={previous}
                      >
                        <CarouselIndicators
                          items={items}
                          activeIndex={activeIndex}
                          onClickHandler={goToIndex}
                        />
                        {items.map(item => {
                          return (
                            <CarouselItem
                              onExiting={onExiting}
                              onExited={onExited}
                              key={item.src}
                            >
                              <img src={item.src} alt={item.altText} />
                              <CarouselCaption
                                captionText={item.caption}
                                captionHeader=""
                              />
                            </CarouselItem>
                          );
                        })}
                        <a
                          className="left carousel-control carousel-control-prev"
                          data-slide="prev"
                          href="#pablo"
                          onClick={e => {
                            e.preventDefault();
                            previous();
                          }}
                          role="button"
                        >
                          <span className="fa fa-angle-left" />
                          <span className="sr-only">Previous</span>
                        </a>
                        <a
                          className="right carousel-control carousel-control-next"
                          data-slide="next"
                          href="#pablo"
                          onClick={e => {
                            e.preventDefault();
                            next();
                          }}
                          role="button"
                        >
                          <span className="fa fa-angle-right" />
                          <span className="sr-only">Next</span>
                        </a>
                      </Carousel>
                    </Card>
                  </Col>
                  <Col className="ml-auto mr-auto" md="4">
                    <p className="description">
                        {descripcion}
                    </p>
                  </Col>
                </Row>
              </Container>
            </div>{" "}
          </>
        )
      }}
    </Consumer>
  );
}

export default SectionCarousel;
