import React, {useState} from "react";
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import IndexHeader from "components/Headers/IndexHeader.js";
import DemoFooter from "components/Footers/DemoFooter.js";
import Nosotros from "components/Content/Nosotros.js";
import Contact from "components/Content/Contact.js";
import Servicios from "components/Content/Servicios.js";
import PreFooter from "components/Content/PreFooter.js";
import Mapa from "components/Content/Mapa.js";
import Galeria from "components/Content/Galeria.js";

//importamos el contexto
import { Provider } from 'Config/Context';
import Config  from 'Config/Config';
import { Element } from 'react-scroll';

function Index() {
  const [idioma,setIdioma] = useState(localStorage.getItem("idioma") || "es");

  function handleClick(e){
    let idioma =  e.target.id;
    if(idioma){
      setIdioma(idioma);
      localStorage.setItem("idioma",idioma);
      // console.log(idioma)
    }else{
      console.log("Ocurrio algo "  +  idioma)
    }
  }

  document.documentElement.classList.remove("nav-open");
  React.useEffect(() => {
    document.body.classList.add("index");
    return function cleanup() {
      document.body.classList.remove("index");
    };
  });
  return (
    <Provider value={{
        idioma:idioma,
        handleClick:handleClick,
        Config:Config
    }}>

      <>
        <Element name={"seccion-principal"}>
          <IndexNavbar />
          <IndexHeader />
        </Element>
        <div className="main">
          <Element name={"seccion-nosotros"}>
            <Nosotros />
          </Element>
          <Element name={"seccion-servicios"}>
            <Servicios/>
          </Element>
          <Element name={"seccion-galeria"}>
            <Galeria />
          </Element >
          <Element name={"seccion-contactos"}>
            <Contact />
          </Element>
          <Element name={"seccion-mapa"}>
            <Mapa />
          </Element>
          <Element name={"seccion-footer"}>
            <PreFooter />
            <DemoFooter />
          </Element>
        </div>
      </>
    </Provider>
  );
}

export default Index;
