import React from "react";
import * as animaciones from 'react-animations';
import {merge} from 'react-animations';
import styled, { keyframes } from 'styled-components';

const defaultProps = {
	  animacion: "bounce",
	  tiempo: "2s",
	  clase:"",
	  tag:"div",
	  href:"",
	  animes: ["tada","filp"],
	  infinite:false
};

//const tadaFlip = merge(animaciones["rollIn"], animaciones["rollOut"],animaciones["fadeOut"]);

function bounceAnimation(animacion){
	let kf
	//return keyframes`${animaciones[animacion]}`; 
	//return keyframes`${tadaFlip}`; 
	if(Array.isArray(animacion)){
		//hacer un merge
		let fusion = merge(animaciones[animacion[0]], animaciones[animacion[1]])
		//let fusion2 = merge(animaciones["rollOut"], animaciones["fadeOut"])
		//let fusion3 = merge(fusion,fusion2)
		kf=keyframes`${fusion}`
	}else{
		//normal
		kf = keyframes`${animaciones[animacion]}`
	}
	return kf
} 


const Container = styled.div`
	animation: ${props => props.tiempo} ${props => bounceAnimation(props.animacion)} ${props=>props.infinite?"infinite":""};
`;

function Animate(props){
	let props_ =  Object.assign({},props,props.config)
	let {tag,clase,children} = props_;
	// console.log(props_)
  	return <Container as={tag} className={clase} {...props_}>{children}</Container>;
};

Animate.defaultProps = defaultProps;
export default Animate;